import React, { useEffect, useState } from "react";

import Axios from "axios";

import MinasGeraisImg from "../../../imgs/Minas Gerais.png";
import SaoPauloImg from "../../../imgs/So Paulo.png";
import RioDeJaneiroImg from "../../../imgs/Rio de Janeiro.png";
import EspiritoSantoImg from "../../../imgs/Esprito Santo.png";
import ParanaImg from "../../../imgs/Paran.png";
import RondoniaImg from "../../../imgs/Rondnia.png";
import BahiaImg from "../../../imgs/bahia.png";

import CafeGourmetImg from "../../../imgs/cafegourmet.png";
import Combo_CafesGourmetImg from "../../../imgs/Combo_CafesGourmet.png";
import CapsulaDeCafeImg from "../../../imgs/capsula-de-cafe-espresso-pimpinela-gourmet-tres-01.png";
import CafeteiraImg from "../../../imgs/cafeteira.png";

import DatabaseLiSection from "./TopBar/DatabaseLiSection";
import ManualLilSection from "./TopBar/ManualLiSection";
import SearchBar from "./TopBar/SearchBar/SearchBar";
import displayError from "../../../globalFunctions/displayErrors";

const TopBar = () => {
  const [categories, setCategories] = useState([]);
  const [categoriesName, setCategoriesName] = useState([]);

  const { serverUrl } = require("../../../connection.json");

  useEffect(() => {
    const fetchCategories = async () => {
      try {
        const { data } = await Axios.get(`${serverUrl}/categories/get`);

        if (data.isError) return displayError(data.errorCode, data.errno);
        setCategories(data);

        const getCategoriesNames = data.map((category) => {
          return category.categoryName;
        });

        setCategoriesName(getCategoriesNames);
      } catch (err) {
        return displayError(err.message, err.code);
      }
    };
    fetchCategories();
  }, [serverUrl]);

  const defaultCategoryValue = [
    {
      categoryId: "discount",
      categoryName: "Com Desconto",
    },
    {
      categoryId: "combos",
      categoryName: "Combos",
    },
    {
      categoryId: "freefreight",
      categoryName: "Frete grtis",
    },
  ];

  if (!categoriesName) {
    return null;
  }

  return (
    <main className="topbar">
      <main className="consult_bar">
        <ul>
          <DatabaseLiSection
            className="novidades"
            category="news"
            classId="novidades_box"
            liTitles={categoriesName}
            imgSrc={CafeGourmetImg}
            imgAlt="cafegourmet-oncoffee"
            categories={categories}
            loadSubcategories={true}
          >
            Novidades
          </DatabaseLiSection>

          <DatabaseLiSection
            className="promooes"
            category="promotions"
            classId="promo_box"
            liTitles={defaultCategoryValue}
            imgSrc={Combo_CafesGourmetImg}
            imgAlt="combo-cafesgourmet-oncoffee"
            categories={categories}
            loadSubcategories={false}
          >
            Promoes
          </DatabaseLiSection>

          <ManualLilSection
            className="cafes"
            title="Cafs"
            classId="cafes_box"
            imgSrc={CapsulaDeCafeImg}
            imgAlt="capsuladecafe-oncoffee"
          >
            <ul className="cafe_estado">
              <a href="/">Por Estado</a>
              <div className="cafe_estadoul">
                <li>
                  <a href="/">
                    Esprito Santo{" "}
                    <img src={EspiritoSantoImg} alt="espirito-santo-flag" />
                  </a>
                </li>
                <li>
                  <a href="/">
                    Minas Gerais{" "}
                    <img src={MinasGeraisImg} alt="minas-gerais-flag" />
                  </a>
                </li>
                <li>
                  <a href="/">
                    Rio de Janeiro{" "}
                    <img src={RioDeJaneiroImg} alt="rio-de-janeiro-flag" />
                  </a>
                </li>
                <li>
                  <a href="/">
                    So Paulo <img src={SaoPauloImg} alt="sao-paulo-flag" />
                  </a>
                </li>
                <li>
                  <a href="/">
                    Paran
                    <img src={ParanaImg} alt="paran-flag" />
                  </a>
                </li>
                <li>
                  <a href="/">
                    Rondnia <img src={RondoniaImg} alt="rondnia-flag" />
                  </a>
                </li>
                <li>
                  <a href="/">
                    Bahia <img src={BahiaImg} alt="bahia-img" />
                  </a>
                </li>
              </div>
            </ul>
            <ul>
              <a href="/">Mtodos</a>
              <li>
                <a href="/">Gros</a>
              </li>
              <li>
                <a href="/">Modos</a>
              </li>
              <li>
                <a href="/">Cpsulas</a>
              </li>
              <li>
                <a href="/">Solveis</a>
              </li>
              <li>
                <a href="/">Sachs</a>
              </li>
              <li>
                <a href="/">Drip Coffee</a>
              </li>
              <li>
                <a href="/">Cold Brew</a>
              </li>
              <li>
                <a href="/">Infusores</a>
              </li>
              <li>
                <a href="/">Ver Mais...</a>
              </li>
            </ul>
            <ul>
              <a className="tipos_a" href="/">
                Tipos
              </a>
              <li>
                <a href="/">Aromatizados</a>
              </li>
              <li>
                <a href="/">Orgnicos</a>
              </li>
              <li>
                <a href="/">Microlote</a>
              </li>
              <li>
                <a href="/">Descafeinado</a>
              </li>
              <a className="intenso_a" href="/">
                Intensidade
              </a>
              <li>
                <a href="/">Suave</a>
              </li>
              <li>
                <a href="/">Mdia</a>
              </li>
              <li>
                <a href="/">Intensa</a>
              </li>
            </ul>
          </ManualLilSection>

          <ManualLilSection
            className="cafeteiras"
            title="Cafeteiras"
            classId="cafeteiras_box"
            imgSrc={CafeteiraImg}
            imgAlt="cafeteria-oncoffee"
          >
            <ul>
              <a href="/">Espresso</a>
              <li>
                <a href="/">Maquinas para Gros</a>
              </li>
              <li>
                <a href="/">Maquinas para Ps</a>
              </li>
              <li>
                <a href="/">Maquinas para Capsulas</a>
              </li>
              <a className="percoa" href="/">
                Percolao
              </a>
              <li>
                <a href="/">Cafeteira Globinho</a>
              </li>
              <li>
                <a href="/">Cafeteira Italiana</a>
              </li>
            </ul>
            <ul>
              <a href="/">Filtrados</a>
              <li>
                <a href="/">Prensa Francesa</a>
              </li>
              <li>
                <a href="/">Aeroexpress</a>
              </li>
              <li>
                <a href="/">Hario v60</a>
              </li>
              <li>
                <a href="/">Chemex</a>
              </li>
              <li>
                <a href="/">Eva Solo</a>
              </li>
              <li>
                <a href="/">Moka</a>
              </li>
              <li>
                <a href="/">Coador de Papel</a>
              </li>
              <li>
                <a href="/">Coador de Pano</a>
              </li>
              <li>
                <a href="/">Ver Mais...</a>
              </li>
            </ul>
          </ManualLilSection>

          <DatabaseLiSection
            className="acesso"
            classId="acesso_box"
            category="acessories"
            liTitles={defaultCategoryValue}
            imgSrc={Combo_CafesGourmetImg}
            imgAlt="combo-cafesgourmet-oncoffee"
            categories={categories}
            loadSubcategories={true}
          >
            Acessrios
          </DatabaseLiSection>

          <DatabaseLiSection
            className="derivados"
            classId="derivados_box"
            category="derivatives"
            liTitles={defaultCategoryValue}
            imgSrc={Combo_CafesGourmetImg}
            imgAlt="combo-cafesgourmet-oncoffee"
            categories={categories}
            loadSubcategories={true}
          >
            Derivados
          </DatabaseLiSection>
        </ul>
      </main>
      <SearchBar />
    </main>
  );
};

export default TopBar;
